//  - top_area.jsx
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";

//module
import Logo from "../modules/Logo";
import Topbtn from "../modules/Topbtn";
import SwiperRollben from "../plugin/Swiper_rollben";

/////////data
import { gnbData, gnbDataR } from "../data/gnb_data";
import SearchInner from "../modules/SearchInner";

/////import area///////////////////////////////////////////////////////////

export default function TopArea() {
  // ////////////
  //1.   
  // const showSearch = (e) => {
  //   // 
  //   e.preventDefault();
  //   //1. 
  //   $(".search-inner").show();
  //   //show() -> display 
  //   //2.   
  //   $("#schinGnb").focus();
  // }; ////////showSearch

  const showMenu = (e) => {
    // const searchBtn = $(".search-btn");
    //  
    const tg = $(".search-inner");
    tg.stop().animate(
      {
        right: 0,
      },
      500
    );

    // BG 
    // $(".blocking-bg").fadeIn(300);
    $(".blocking-bg").show().css({
      display: "block",
    }).animate({opacity: 1,},200)

    //        
      $("body,html").css({ overflow: "hidden" }); // body  
    
  };

  /////
  return (
    <>
      {/* <!--follow  --> */}
      <Topbtn />
      {/*   search-inner  >   */}
      <SearchInner />
      
      <header id="header-area">
        {/* <!--  --> */}
        <SwiperRollben />
        {/* <!-- GNB area --> */}
        <div className="fx-box" id="gnb-area">
          {/* <!-- L menu --> */}
          <div className="fx-box" id="L-menu-wrap">
            <ul className="menu-inner fx-box">
              {gnbData.map((v, i) => (
                <li key={i} className="sub-menu">
                  {
                    // : a cnffur / : Link  
                    v.sub ? (
                      <Link to={v.link}>{v.txt}</Link>
                    ) : (
                      <Link to={v.link}>{v.txt}</Link>
                    )
                  }
                  {
                    //   >   
                    v.sub && (
                      <div className="sub-inner">
                        <ol>
                          {v.sub.map((v, i) => (
                            <li key={i}>
                              <Link to={v.link}>{v.txt}</Link>
                            </li>
                          ))}
                        </ol>
                      </div>
                    )
                  }
                </li>
              ))}
            </ul>
          </div>
          {/* <!-- L menu end --> */}
          <div className="logo">
            <Link to="/">
              <Logo logoStyle="top" />
            </Link>
          </div>
          {/* <!-- R menu --> */}
          <div className="fx-box" id="R-menu-wrap">
            <ul className="fx-box sub-menu">
              {gnbDataR.map((v, i) => (
                <li key={i} className="">
                  <Link to={v.link}>{v.txt}</Link>
                </li>
              ))}
              {/* <!-- icon box --> */}
              <ul className="icon-box fx-box">
                <li className="icon-item fx-box">
                  <Link to="">
                    <span className="icon-img mall material-symbols-outlined">
                      local_mall
                    </span>
                    {/* <!-- <span className="mall-cunt">(0)</span> --> */}
                  </Link>
                </li>
                <li className="icon-item">
                  <Link to="">
                    <span className="icon-img material-symbols-outlined">
                      person
                    </span>
                  </Link>
                </li>
                <li className="icon-item search-box">
                  {/* <Link to=""> */}
                    <span
                      className="icon-img material-symbols-outlined search-btn"
                      onClick={showMenu}
                    >
                      search
                    </span>
                  {/* </Link> */}
                </li>
              </ul>
              {/* <!-- icon-box end --> */}
            </ul>
          </div>
          {/* <!-- R menu end --> */}
        </div>
      </header>
    </>
  );
} /////TopArea////////////
