import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import $ from "jquery";

////module
import { sCon } from "./sCon";
import introFn from "../func/intro";

import "../../css/intro.scss";
//////////////////import area///////////////////////

export default function Intro() {
    const bar = $(".loading");
    // console.log("", bar);
    const [IsLoading, setIsLoading] = useState(true);

    // const introArea = document.getElementById('intro-area');
    // introArea.style.cursor = `url('/images/cursor.svg'), auto`; //    
    
    useEffect(() => {
        
      const checkResourcesLoaded = async () => {
        // ,       
        await Promise.all([
          new Promise((resolve) => {
            const img = new Image();
            img.src = "/images/icon-sim-white.svg";
            img.onload = resolve;
          }),
          // ... (   )
        ]);
    
        setIsLoading(false); //      state 
      };
    
      checkResourcesLoaded();
    }, []);
    let myCon = useContext(sCon);

    //////   //////////////
    return (
        <>
            <div id="intro-area">
                {!IsLoading && (
                    <Link
                        to="/"
                        onClick={(e) => {
                            e.preventDefault();
                            myCon.setIntroSts(false);
                        }}
                    >
                        <div className="cont-wrap">
                            <span>
                                <img src={"../images/icon-sim-white.svg"} alt="" />
                            </span>
                            <img src={"../images/logo_ko.svg"} alt="" />
                            <div className="desc">
                                 (lorem ipsum;  , lipsum)
                                    ,
                                ,  
                            </div>
                            <div className="loading"></div>
                        </div>
                        <div className="bg">
                            <video id="myvid" muted autoPlay loop>
                                <source
                                    src={"../images/intro_bg1.mp4"}
                                    type="video/mp4"
                                />
                            </video>
                        </div>
                    </Link>
                )}
            </div>
        </>
    ); ///////return
}
