// shop   ///
// 
import { bestItemData } from "../data/slide_item_data";
import { sCon } from "./sCon";
//CSS
import "../../css/shop.scss";
import Category from "../modules/Category";
import { Link } from "react-router-dom";
import ShopPdList from "../modules/ShopPdList";
import { product } from "../data/product";
import { useContext, useState } from "react";
/////import area/////////////////////////////////////////////////

export default function Shop() {
  const myCon = useContext(sCon);


  //activeCat =  
  const activeCat = (cat) =>{
    myCon.setSelCat(cat); //    
  }


  ////   //////////////
  return (
    <>
      <div className={`sub-header bg1 cbx bgi`}>
        <div className="title">
          <div className="icon">
            <img src={process.env.PUBLIC_URL+"/images/icon-sim-black.svg"} alt="" />
          </div>
          <h3>NOTICE</h3>
          <span>  </span>
        </div>
      </div>

      <section id="sell-pd-list" className="cont-box">
        {/*   */}
          <Category onCat={(cat)=>activeCat(cat)} selCat={myCon.selCat}/>
        {/*  */}
        <ShopPdList products={product[myCon.selCat]}/>
        {/* page Num */}
        <div className="page-num fx-box">
          <div className="prev">
            <span className="icon-img material-symbols-outlined">
              chevron_left
            </span>
          </div>
          <ul className="fx-box">
            <li className="select-ef">
              <Link to="">1</Link>
            </li>
            <li>
              <Link to="">2</Link>
            </li>
            <li>
              <Link to="">3</Link>
            </li>
            <li>
              <Link to="">4</Link>
            </li>
            <li>
              <Link to="">5</Link>
            </li>
          </ul>
          <div className="next">
            <span className="icon-img material-symbols-outlined">
              chevron_right
            </span>
          </div>
        </div>
      </section>
    </>
  );
} /////////// Shop /////////////////////
