//pj    ////////////////

import React from "react";
import { Link } from "react-router-dom";

//css
import "../../css/search.scss";
import SwiperPrdList from "../plugin/Swiper_prdList";
///import area//////////////////////////////

function SearchingProduct({ dt }) {
  console.log("dt", dt);
  // console.log("dt img",dt.img[0]);
  //dt :  
  //total =   
  const total = dt.length;
  console.log("", total);

  //dt: Searching  
  return (
    <>
      <section className="plist-wrap">
        {/* case1.    */}
        {total > 0 && (
          <ul className="plist">
            {dt.map((v, i) => (
              <li key={i}>
                <Link
                  to="/detail"
                  state={{
                    topcat: v.category, // 
                    product: v.idx, // 
                  }}
                >
                  <div className="desc-wrap">
                    <div className="eng-pdtit">{v.engtit}</div>
                    <div className="kor-pdtit">{v.tit}</div>
                    <div className="desc">{v.txt}</div>
                  </div>
                  <div className="img-wrap">
                    <div className="imgbx">
                      <img src={process.env.PUBLIC_URL+`${v.img[0].isrc}`} alt={`"${v.tit}"`} />
                    </div>
                    <div className="blur">
                      <img src={process.env.PUBLIC_URL+`${v.img[1].isrc}`} alt={`"${v.tit}"`} />
                    </div>
                  </div>
                </Link>
              </li>
            ))}
          </ul>
        )}

        {/* case2.    */}
        {
        total == 0 && (
          <>
            <h2>   .</h2>
            <section id="prd-list">
              <div className="desc-box">
                <div className="tit">lorem ipsum</div>
                <span className="desc">
                   (lorem ipsum;  , lipsum) 
                </span>
              </div>
              {/*   swiper : Swiper_prd-list */}
              <SwiperPrdList />
            </section>
          </>
        )}
      </section>
    </>
  );
}

export default SearchingProduct;
