// 
import React from "react";
import { Link, useNavigate } from "react-router-dom";
import $ from "jquery";

//module
import Logo from "./Logo";

//css
import "../../css/search.scss";
import SwiperBestItem from "../plugin/Swiper_bestItem";
//////import area/////////////////////////////////////////

function SearchInner() {
  const goNav = useNavigate();

  // $('body,html').css('overflow', 'hidden'); // body  
  const searchClose = () => {
    //
    // const tg = $("body,html");

    //1.    
    // $(".close-btn").click(() => {
    //   tg.css({ overflow: "auto" }); // body  
    // });

    // BG 
    $(".blocking-bg").fadeOut(200);

    //2.     
    $(".search-inner").stop().animate(
      {
        right: "-72vw",
      },
      500
    );
    $("body,html").css({ overflow: "auto" });
  };

  //2.     
  const enterkey = (e) => {
    //e,keyCode =  
    //e,key =  
    // console.log(e.keyCode);
    if (e.key == "Enter") {
      //   : val()
      let txt = $(e.target).val().trim();
      console.log(txt);

      //   ( )
      if (txt != "") {
        //   
        $(e.target).val("").parent().parent().parent().css({right:"-72vw"});
        console.log(" ?", e.target);
        // 
        goSearch(txt);
        $(".blocking-bg").fadeOut(200);
        $("body,html").css({ overflow: "auto" });
      } ///if
    } ////if
  };

  //3.    
  const goSearch = (txt) => {
    console.log("  ^-^");
    //  
    //  (,{state:{}})
    goNav("search", { state: { keyword: txt } });
  };

  /////// /////////////////////////////
  return (
    <>
      <div className="search-inner">
        <div className="search-box">
          <div className="search-header fx-box">
            <div className="logo">
              <Link to="/">
                <img src={process.env.PUBLIC_URL+"/images/logo_en_B.png"} alt="" />
              </Link>
            </div>
            <button className="close-btn" onClick={searchClose}>
              <span className="icon-img material-symbols-outlined search-btn">
                close
              </span>
            </button>
          </div>

          <div className="input-box fx-box">
            <input
              id="schinGnb"
              type="text"
              placeholder=" "
              onKeyUp={enterkey}
              onClick={(e) => {
                let stxt = e.currentTarget.nextElementSibling.value;
                //nextElementSibling:       null        
                if(stxt.trim!="")goSearch(stxt);
                else{
                  alert(" ");
                }
                
              }}
            />
            <button className="search-btn"
            onClick={enterkey}
            >
              <span className="icon-img material-symbols-outlined search-btn">
                search
              </span>
            </button>
          </div>

          {/*   or   */}
          <div className="recent-search-list">
            <h4> </h4>
            <ul>
              <li>
                <Link to="">
                  <span>  </span>
                </Link>
              </li>
              <li>
                <Link to="">
                  <span> </span>
                </Link>
              </li>
              <li>
                <Link to="">
                  <span> </span>
                </Link>
              </li>
              <li>
                <Link to="">
                  <span> </span>
                </Link>
              </li>
            </ul>
          </div>
        </div>
        <section className="best-item-ban">
          {/* <SwiperBestItem /> */}
        </section>
      </div>
      <div className="blocking-bg"></div>
    </>
  );
}

export default SearchInner;
